
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Countup from 'vux/src/components/countup/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Card from 'vux/src/components/card/index.vue'

import ViewBox from '../../node_modules/vux/src/components/view-box/index.vue'

export default {
directives: {
TransferDom
},
components: {
ViewBox,
XHeader,
XButton,
Box,
XInput,
Group,
Cell,
Toast,
Tabbar,
TabbarItem,
Flexbox,
FlexboxItem,
Divider,
Countup,
Swiper,
SwiperItem,
Confirm,
XSwitch,
Datetime,
Card
},
computed: {
leftOptions () {
return {
showBack: true,
backText: ''
}
},
rightOptions () {
return {
showMore: false
}
}
},
mounted () {
this.$vux.loading.show({
text: 'loading'
})
this.getUserInfo()
this.todayDate = this.getNowFormatDate() 
this.todayQuestion()
this.failureShowToast = false
this.changeSuccessShow = false
},
data () {
return {
title: 'Sign up',
showConfirmBox: false,
todayDate: '2017-12-28',
computeDaysValue: '',
totalScore: 0,
computeHoursFunction (date, isToday, generateRange) {
if (isToday) {
return generateRange(new Date().getHours(), 23)
} else {
return generateRange(0, 23)
}
},
computeDaysFunction (options, generateRange) {
return [options.month] 
},
dateTime: '2017-01-26',
questionTitle: '',
moreLink: {title: 'More', link: 'http://www.google.com'},
questionDate: {title: 'Question list:'},
answerContent: '<p style="color:#000">hehheheehe</p>',
mobile: '',
password: '',
iconType: '',
successText: '',
changeSuccessShow: false,
changeFailureShow: false,
startCount: 0,
endCount: 100,
totalPages: 0,
failureText: '',
timeLine: 0,
isAnswerToday1: false,
isAnswerToday: false,
answerPeopleList: [
{name: 'Tony', getCoins: 20},
{name: 'Mike', getCoins: 20},
{name: 'Xiao', getCoins: 20},
{name: 'Tom', getCoins: 20}
]
}
},
methods: {
goRedeem () {
this.$router.push('/redeem')
},
todayQuestion () {
this.$http.post(this.$apis.todayQuestion, {isToday: true}).then(res => {
if (res.code === 200) {
this.questionTitle = res.data.title
} else {
this.questionData = '<p>Today\'s question is not ready, please wait for a minute!</p>'
}
}).catch(function (error) {
console.log('Error: ' + error)
})
},
goRegulation () {
this.$router.push('regulation')
},
goQuestion () {
if (this.isAnswerToday1) {
this.isAnswerToday = true
this.showConfirmBox = false
} else {
this.showConfirmBox = true
}
},
onIndexChange (newIndex, oldIndex) {
console.log(newIndex, oldIndex)
},
onCancel () {
},
onConfirmQuestion () {
this.$router.push('question')
},
onConfirm () {
this.$router.push('questionlist')
},
onShow () {
this.$http.post(this.$apis.todayQuestion, {isToday: true}).then(res => {
if (res.code === 200) {
this.timeLine = res.data.clock 
this.$vux.loading.hide()
}
}).catch((error) => {
console.log('Error: ' + error)
this.$vux.loading.hide()
})
},
getNowFormatDate (Today) {
let day = 0
if (Today === 'Today') {
day = 1
}
let date = new Date()
let seperator1 = '-'
let year = date.getFullYear()
let month = date.getMonth() + 1
let strDate = date.getDate() - day  
if (month >= 1 && month <= 9) {
month = '0' + month
}
if (strDate >= 0 && strDate <= 9) {
strDate = '0' + strDate
}
let currentdate = year + seperator1 + month + seperator1 + strDate
return currentdate
},
onHide () {

},
change (val) {
console.log('on change', val)
},
onBlur (val) {
console.log('on blur', val)
},
onFocus (val, $event) {
console.log('on focus', val, $event)
},
onEnter (val) {
console.log('click enter!', val)
},
getUserInfo () {
let userId = window.localStorage.getItem('userId')
this.$http.post(this.$apis.userInfo, {userId: userId})
.then((res) => {
this.totalPages = res.data.readPages
this.totalScore = res.data.score
this.isAnswerToday1 = res.data.isAnswerToday
this.$vux.loading.hide()
})
.catch(function (error) {
console.log(error)
this.$vux.loading.hide()
})
}
}

}



